*{
    margin: 0;
    padding: 0;
}

.wrapper{
    display: flex;
    align-items: center;
    height: 100vh;
    width: 100vw;
    justify-content: center;
}

.sideBar{
    flex: 1;
    height: 97vh;
    background-color: #eee;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 1px #ccc;
    margin: 10px;
    
    width: fit-content;
}

.avatar{
    position: relative;
    padding: 20px 20px;
    width: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.imgBx {
    position: relative;
    border-radius: 50%;
    object-fit: cover;
    width: 140px;
    height: 140px;
    object-fit: cover;
    box-shadow:  0 0 5px 1px #1118;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.imgBx img{

    width: 70%;
    height: 70%;
}
.avatar span{
    padding: 5px 0px;
    font-size: 18px;
    font-weight: 600;
    color: #635e5e;
}

.sideBar-options{
    list-style: none;
    display: flex;
    justify-content:center;
    align-items: flex-start;
    flex-direction: column;
    padding: 10px;
}

.sideBar-options .sideBar-options-item{
    margin: 10px 20px 15px 20px ;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sideBar-options-item i{
    padding-right:10px;
    font-size: 15px;
    color: #999;
}
.sideBar-options .sideBar-options-item a{
     text-decoration: none;
     color: #999;
     font-weight: 400;
     font-size: 14px;
}
.sideBar-options .sideBar-options-item .active{
    color: #111;
    text-shadow:  0 0 2px  #111;
}
.sideBar-options .sideBar-options-item a:hover{
    color: #111;
}
.view{
    width: 850px;
    border-radius: 10px;
    height: 97vh;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.view h2{
    font-size: 25px;
    padding: 20px 10px 20px 10px;
}
.introduccion{
    padding: 20px 0px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}

.introduccion .about{
   background-color: #eee;
   padding: 20px 15px;
   font-size: 15px;
   font-weight: lighter;
   border-radius: 10px;
   box-shadow: 0px 0px 5px 1px #ccc;
   margin: 0 40px 0px 0;
   width: 150px;
   height: fit-content;
}

.instrucciones{
     
    width: 400px;
    height: fit-content;
    border-radius: 10px;
    padding:15px;
    box-shadow: 0px 0px 5px 1px #ccc;
    background-color: #eee;
}

.instrucciones h3{
    font-size: 20px;
    color: #888;
    padding: 5px 10px;
}

.instrucciones p{
    font-size: 15px;
    color: #888;
    padding: 0 10px 10px 10px;
}

.container-newAccount{
    width: 50%;
    height: 97vh;
    padding: 0px 10px;
    background-color: #eee;
    border-radius: 10px;
    box-shadow: 0 0 5px 1px #ccc;
}

.container-newAccount h4{
    text-align: center;
    padding: 30px 0;
    font-size: 20px;
    font-weight: bolder;
    color: #999;
    text-shadow: 0 0 3px #222;
}

.data-new-acc{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 10px;
}
.contents{
 display: flex;
 flex-direction: column;
 padding: 20px 0;
}
.contents label{
    font-size: 15px;
    font-weight: unset;
    color: #635e5e;
    padding: 8px 0;
    text-shadow: 0 0 2px rgb(20, 25, 25);
}
.contents input{
    border: none;
    border-radius: 5px;
    padding: 0 10px;
    width:300px;
    height: 40px;
    font-size: 13px;
    font-weight: lighter;
    background-color: #ddd;
    outline: none;
}

#createAcc{
    cursor: pointer;
    width: fit-content;
    height: fit-content;
    padding: 5px 10px;
    font-size: 15px;
    border: none;
    background-color: #ddd;
    border-radius: 5px;
    transition: 0.3s;
}

#createAcc:hover{
    background-color: #222;
    color: #fff;
}

.content-add{
    width: 90%;
    padding: 10px;
    border-radius: 10px;
    background: #eee;
    box-shadow: 0 0 5px 1px #ccc;
}

.options-btn{
    padding: 10px 0;
    background-color: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.options-btn button{
    font-size: 20px;
    text-shadow: 0 0 2px #222;
    color: #ccc;
}

.name-account-client{
    box-shadow: 0 0 5px 1px #ccc;
    background-color: #eee;
    width: 90%;
    margin: 10px 0px;
    padding: 10px;
    border-radius: 10px;
    text-shadow: 0 0 2px #333;
    text-align: center;
}

.container-accounts h3{
    margin: 0;
    padding: 10px;
    width: 90%;
    text-align: center;
    color: #635e5e;
    text-shadow: 0 0 2px #222;
    background-color: #eee;
    margin: 10px 0;
    box-shadow: 0 0 5px 1px #ccc;
    border-radius: 10px;
}

.resultados-accounts{
       background-color: #eee;
       box-shadow: 0 0 5px 1px #ccc;
       border-radius: 10px;
       width: 90%;
}


@media screen  and (max-width : 1100px ) {
  
    #root{
      flex-direction: column;
    }
    .sideBar{
     display: flex;

 
    }
    .view{
        align-items:center;

    }
    .container-accounts{
      width: 90%;
    }
 
 }
 
 
 @media screen and (max-width : 450px ){
 
   .sideBar{
     width: 90%;
     height: fit-content;
   }
  .view{
     align-items: center;
  }
 }

 
 @media screen and (max-width : 400px ){
 
    .sideBar{
      width: 90%;
      height: fit-content;
      flex-direction: column;
      align-items: center;
    }

    .options-btn{
        flex-direction: column;
    }
    .contents input{
        width: 100%;
    }
    .container-newAccount{
        width: 300px;
    }
  }