body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
   
  display: flex;
  justify-content: center;
  align-items: center;
}


a{
  text-decoration: none;
  font-size: 12px;
  font-weight: 400;
  color: #2228;
  padding: 5px;
  border-radius: 5px;
  transition: .3s;
}

.sideBar-options-item.active{
  color: #222;
  text-shadow: 0 0 2px #ddd;
  box-shadow: 0 5px 5px 1px rgb(83, 81, 81);
}

.aspa{
  width: 250px;
  height: 250px;
}

.feed{
  width: 70%;
  border-radius: 8px;
  margin: 0 50px;
  background-color: #eee;
  padding: 20px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.consejo{
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  margin: 10px 0;
  color: rgb(69, 73, 67);
  font-size: 15px;
}

.redireccion{
  font-size: 20px;
  font-weight: 600;
  color: rgb(109, 192, 109);
}

.update-avatar{
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    position: absolute;
    background-color: #3338;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.update-avatar input{
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   opacity: 0;
   height: 100%;
}

.update-avatar img{
  filter: invert(0.9);
  width: 60px !important;
  height: 60px !important;
  position: relative;
  top: 0px;
}

.update-avatar p{
  position: relative;
  top: 5px;
  font-size: 17px;
  font-weight: lighter;
  color: #ddd;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.imgBx:hover .determinado{
  opacity: 0;
  visibility: hidden;
}
.imgBx:hover .update-avatar{
  cursor: pointer;
  opacity: 1;
  visibility: visible;
}

img:hover ~ img{
  transform: scale(1.1);
}

.btn_to_resultado{
  color: rgb(106, 75, 75);
  text-shadow:   0 0  2px rgb(72, 68, 60);
  font-size: 20px;
  font-weight: 500;
}

.redireccion{

  line-height: 15px;
}

.redireccion:hover{
  border-bottom: 4px solid;
  
}

@media screen  and (max-width : 1100px ) {
  
   #root{
     flex-direction: column;
   }
   .sideBar{
    display: flex;

   }
   .container-accounts{
     width: 90%;
   }

}


@media screen and (max-width : 450px ){

  .sideBar{
    width: 90%;
    height: fit-content;
  }

}